
'use server';
/**
 * @fileOverview Saves extracted PDF data to a Google Sheet.
 *
 * - saveToGoogleSheet - A function that handles saving data to Google Sheets.
 * - SaveToGoogleSheetInput - The input type for the saveToGoogleSheet function.
 * - SaveToGoogleSheetOutput - The return type for the saveToGoogleSheet function.
 */

import { ai } from '@/ai/genkit';
import { z } from 'genkit';
import { ExtractedPdfDataSchema } from '@/ai/schemas/pdf-data-schema';
import { google } from 'googleapis';

const SaveToGoogleSheetInputSchema = z.object({
  extractedData: ExtractedPdfDataSchema.describe("The structured data extracted from the PDF."),
});
export type SaveToGoogleSheetInput = z.infer<typeof SaveToGoogleSheetInputSchema>;

const SaveToGoogleSheetOutputSchema = z.object({
  success: z.boolean().describe('Whether the operation was successful.'),
  message: z.string().optional().describe('A message detailing the outcome or error.'),
  sheetUrl: z.string().optional().describe('The URL of the Google Sheet if successful.'),
});
export type SaveToGoogleSheetOutput = z.infer<typeof SaveToGoogleSheetOutputSchema>;

export async function saveToGoogleSheet(input: SaveToGoogleSheetInput): Promise<SaveToGoogleSheetOutput> {
  return saveToGoogleSheetFlow(input);
}

const saveToGoogleSheetFlow = ai.defineFlow(
  {
    name: 'saveToGoogleSheetFlow',
    inputSchema: SaveToGoogleSheetInputSchema,
    outputSchema: SaveToGoogleSheetOutputSchema,
  },
  async (input) => {
    console.log('Attempting to save data to Google Sheet:', JSON.stringify(input.extractedData, null, 2).substring(0, 500));

    const sheetId = process.env.GOOGLE_SHEET_ID;
    const serviceAccountJsonString = process.env.GOOGLE_SERVICE_ACCOUNT_CREDENTIALS_JSON;
    const targetRange = process.env.GOOGLE_SHEET_TARGET_RANGE || 'Sheet1!A1';

    if (!sheetId) {
      console.error('GOOGLE_SHEET_ID environment variable is not set.');
      return { success: false, message: 'Google Sheet ID is not configured.' };
    }
    if (!serviceAccountJsonString) {
      console.error('GOOGLE_SERVICE_ACCOUNT_CREDENTIALS_JSON environment variable is not set.');
      return { success: false, message: 'Google Service Account credentials are not configured.' };
    }

    try {
      const credentials = JSON.parse(serviceAccountJsonString);
      const auth = new google.auth.GoogleAuth({
        credentials,
        scopes: ['https://www.googleapis.com/auth/spreadsheets'],
      });
      const sheets = google.sheets({ version: 'v4', auth });

      // This order should match your headerRow if you have one and your sheet structure.
      // The `présences` array is stringified to be stored in a single cell.
      const newRowData = [
        input.extractedData.classe,
        input.extractedData.cours,
        input.extractedData.date,
        input.extractedData.nom_du_professeur,
        input.extractedData.nombre_des_présents,
        input.extractedData.salle_n,
        input.extractedData.séance,
        JSON.stringify(input.extractedData.présences || []), 
      ];

      await sheets.spreadsheets.values.append({
        spreadsheetId: sheetId,
        range: targetRange, // e.g., 'Sheet1!A1' - appends after the last row with data in this range.
        valueInputOption: 'USER_ENTERED', // How the input data should be interpreted.
        requestBody: {
          values: [newRowData], // Data must be an array of arrays (each inner array is a row)
        },
      });

      const sheetUrl = `https://docs.google.com/spreadsheets/d/${sheetId}`;
      console.log(`Data successfully appended to Google Sheet: ${sheetUrl}`);
      return { success: true, message: 'Data successfully appended to Google Sheet.', sheetUrl };

    } catch (error: any) {
      console.error('Error appending data to Google Sheet:', error.message, error.stack);
      return { success: false, message: `Failed to save to Google Sheet: ${error.message}` };
    }
  }
);
