
"use client";

import type { FC } from 'react';
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Edit3 } from 'lucide-react';
import type { ExtractedPdfData } from '@/ai/schemas/pdf-data-schema'; // Import the type

interface DataEditorProps {
  data: ExtractedPdfData | null; // Use the specific type
  onDataChange: (updatedData: ExtractedPdfData) => void; // Use the specific type
}

const DataEditor: FC<DataEditorProps> = ({ data, onDataChange }) => {
  const handleChange = (key: string, newValue: string, isJson: boolean) => {
    if (!data) return;

    const updated = { ...data } as ExtractedPdfData; // Cast to ensure type correctness
    
    // Handle specific keys if needed, or generic approach
    const fieldKey = key as keyof ExtractedPdfData;


    if (isJson) { // This typically applies to the 'présences' field or any other field expected to be an array/object
      try {
        updated[fieldKey] = JSON.parse(newValue) as any; // Parse and cast
      } catch (e) {
        // If JSON is invalid during typing, store the raw string for Textarea,
        // but ideally, the schema should guide the type.
        // For simplicity, if it's meant to be an array like 'présences', and parse fails,
        // it might be better to keep it as is or handle error.
        // Storing raw string might break type if not handled carefully.
        // Let's assume for 'présences' it will be an array.
        // If it's a complex object field.
        (updated[fieldKey] as any) = newValue; 
      }
    } else {
      const originalValue = data[fieldKey];
      if (typeof originalValue === 'number') {
        const parsedFloat = parseFloat(newValue);
        (updated[fieldKey] as any) = isNaN(parsedFloat) ? newValue : parsedFloat;
      } else if (typeof originalValue === 'boolean') {
        (updated[fieldKey] as any) = newValue.toLowerCase() === 'true';
      } else {
         (updated[fieldKey] as any) = newValue;
      }
    }
    onDataChange(updated);
  };

  const getDisplayValue = (value: any): string => {
    if (typeof value === 'string') return value;
    if (typeof value === 'number' || typeof value === 'boolean') return String(value);
    if (value === null || value === undefined) return '';
    // If it's an object that was previously stringified due to invalid JSON, show that string
    // otherwise stringify the object.
    return typeof value === 'string' ? value : JSON.stringify(value, null, 2);
  }

  return (
    <Card className="h-full flex flex-col shadow-lg">
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Edit3 className="w-5 h-5 text-primary" />
          Extracted Data
        </CardTitle>
      </CardHeader>
      <CardContent className="flex-grow overflow-y-auto">
        {!data ? (
          <div className="flex flex-col items-center justify-center h-full text-muted-foreground">
            <Edit3 className="w-16 h-16 mb-4" />
            <p className="text-lg">Extracted data will appear here for editing.</p>
          </div>
        ) : (
          <div className="space-y-6">
            {(Object.keys(data) as Array<keyof ExtractedPdfData>).map((key) => {
              const value = data[key];
              return (
                <div key={key} className="space-y-2">
                  <Label htmlFor={key} className="text-base font-semibold text-foreground">
                    {key.replace(/([A-Z])/g, ' $1').replace(/_/g, ' ').replace(/^./, (str) => str.toUpperCase())}
                  </Label>
                  {key === 'présences' || (typeof value === 'object' && value !== null && !Array.isArray(value)) ? ( // Treat 'présences' and other objects as JSON text areas
                    <Textarea
                      id={key}
                      value={getDisplayValue(value)}
                      onChange={(e) => handleChange(key, e.target.value, true)}
                      className="font-mono text-sm rounded-md shadow-sm min-h-[100px]"
                      rows={Math.min(10, getDisplayValue(value).split('\n').length)}
                      aria-label={`Edit ${key} JSON data`}
                    />
                  ) : (
                    <Input
                      id={key}
                      value={getDisplayValue(value)}
                      onChange={(e) => handleChange(key, e.target.value, false)}
                      className="text-sm rounded-md shadow-sm"
                      aria-label={`Edit ${key} value`}
                      type={typeof value === 'number' ? 'number' : 'text'}
                    />
                  )}
                </div>
              );
            })}
          </div>
        )}
      </CardContent>
    </Card>
  );
};

export default DataEditor;
