
"use client";

import { useState, useEffect, type ChangeEvent } from 'react';
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Label } from "@/components/ui/label";
import PdfViewer from "@/components/pdf-viewer";
import DataEditor from "@/components/data-editor";
import { extractDataFromPdf, type ExtractDataFromPdfOutput } from "@/ai/flows/extract-data-from-pdf";
import type { ExtractedPdfData } from "@/ai/schemas/pdf-data-schema"; // Import type from schema file
import { saveToGoogleSheet } from "@/ai/flows/save-to-google-sheet"; 
import { useToast } from "@/hooks/use-toast";
import { UploadCloud, Cpu, FileJson, Loader2, AlertTriangle, FileText, Edit3, Sheet as SheetIcon } from 'lucide-react';

export default function PdfExtractorPage() {
  const [pdfFile, setPdfFile] = useState<File | null>(null);
  const [pdfObjectUrl, setPdfObjectUrl] = useState<string | null>(null);
  const [pdfDataUri, setPdfDataUri] = useState<string | null>(null);
  
  const [extractedData, setExtractedData] = useState<ExtractedPdfData | null>(null);
  
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [isSavingToSheet, setIsSavingToSheet] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);

  const { toast } = useToast();

  useEffect(() => {
    return () => {
      if (pdfObjectUrl) {
        URL.revokeObjectURL(pdfObjectUrl);
      }
    };
  }, [pdfObjectUrl]);

  const handleFileChange = (event: ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file && file.type === "application/pdf") {
      setPdfFile(file);
      setError(null);
      setExtractedData(null); 

      if (pdfObjectUrl) {
        URL.revokeObjectURL(pdfObjectUrl);
      }
      const newObjectUrl = URL.createObjectURL(file);
      setPdfObjectUrl(newObjectUrl);

      const reader = new FileReader();
      reader.onloadend = () => {
        setPdfDataUri(reader.result as string);
      };
      reader.readAsDataURL(file);
      
    } else {
      setPdfFile(null);
      setPdfObjectUrl(null);
      setPdfDataUri(null);
      setError("Please select a valid PDF file.");
      toast({
        title: "Invalid File",
        description: "Please select a valid PDF file.",
        variant: "destructive",
      });
    }
  };

  const handleProcessPdf = async () => {
    if (!pdfDataUri) {
      setError("Please select a PDF file first.");
      toast({
        title: "No PDF Selected",
        description: "Please select a PDF file to process.",
        variant: "destructive",
      });
      return;
    }

    setIsLoading(true);
    setError(null);
    // Do not setExtractedData(null) here, allow previous data to show until new data or error
    // setExtractedData(null); 

    try {
      const result: ExtractDataFromPdfOutput = await extractDataFromPdf({ pdfDataUri });

      if (result && result.jsonOutput && result.jsonOutput.trim() !== '' && result.jsonOutput.trim() !== '{}') {
        try {
          const parsedData: ExtractedPdfData = JSON.parse(result.jsonOutput);
          // console.log('Successfully parsed data in page.tsx:', JSON.stringify(parsedData, null, 2));
          setExtractedData(parsedData);
          toast({
            title: "Data Extracted",
            description: "PDF data has been successfully extracted.",
          });
        } catch (parseError: any) {
          console.error("Error parsing AI output as JSON in page.tsx:", parseError, "Raw output:", result.jsonOutput);
          setError(`Failed to parse AI output. The AI returned malformed data. Raw snippet: ${String(result.jsonOutput).substring(0,100)}...`);
          toast({
            title: "Parsing Failed",
            description: "The data received from the AI could not be understood.",
            variant: "destructive",
          });
          setExtractedData(null); // Explicitly set to null to show placeholder
        }
      } else {
        // This case implies the flow itself returned an empty/invalid string for jsonOutput,
        // which the current extractDataFromPdfFlow tries to avoid.
        const problematicOutput = result ? result.jsonOutput : "No result object from AI flow";
        console.warn("extractDataFromPdf flow returned an empty or unexpected jsonOutput. Full result object:", result, "Actual jsonOutput value received:", problematicOutput);
        setError("AI service returned an empty or improperly structured response. Please try again or check the PDF.");
        toast({
          title: "Empty/Invalid Response",
          description: "The AI service's response was not in the expected format.",
          variant: "destructive",
        });
        setExtractedData(null); // Explicitly set to null
      }
    } catch (err: any) { // Catches errors from extractDataFromPdf() call itself, or if an error was re-thrown from inner blocks
      console.error("Error during PDF processing call in page.tsx:", err);
      const errorMessage = err.message || "An unknown error occurred during PDF processing.";
      setError(errorMessage);
      toast({
        title: "Extraction Process Failed",
        description: errorMessage,
        variant: "destructive",
      });
      setExtractedData(null); // Explicitly set to null
    } finally {
      setIsLoading(false);
    }
  };

  const handleDataChange = (updatedData: Record<string, any>) => {
    setExtractedData(updatedData as ExtractedPdfData);
  };

  const handleDownloadJson = () => {
    if (!extractedData) {
      toast({
        title: "No Data",
        description: "There is no data to download.",
        variant: "destructive",
      });
      return;
    }

    try {
      const jsonString = JSON.stringify(extractedData, null, 2);
      const blob = new Blob([jsonString], { type: "application/json" });
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = url;
      a.download = `${pdfFile?.name.replace(/\.pdf$/i, '') || 'extracted_data'}.json`;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
      toast({
        title: "Download Started",
        description: "Your JSON file is downloading.",
      });
    } catch (e: any) {
        console.error("Error preparing JSON for download:", e);
        toast({
            title: "Download Error",
            description: "Could not prepare data for download. Ensure data is valid JSON.",
            variant: "destructive",
        });
    }
  };

  const handleSaveToSheet = async () => {
    if (!extractedData) {
      toast({
        title: "No Data",
        description: "There is no data to save to Google Sheet.",
        variant: "destructive",
      });
      return;
    }

    setIsSavingToSheet(true);
    try {
      const result = await saveToGoogleSheet({ extractedData });
      if (result.success) {
        toast({
          title: "Saved to Google Sheet",
          description: result.message || "Data successfully saved.",
        });
      } else {
        throw new Error(result.message || "Failed to save to Google Sheet.");
      }
    } catch (err: any) {
      console.error("Error saving to Google Sheet:", err);
      toast({
        title: "Save Failed",
        description: err.message || "An unknown error occurred while saving to Google Sheet.",
        variant: "destructive",
      });
    } finally {
      setIsSavingToSheet(false);
    }
  };

  return (
    <div className="flex flex-col min-h-screen bg-background p-4 md:p-8 selection:bg-primary/20">
      <header className="mb-8">
        <h1 className="text-4xl font-bold text-center text-primary tracking-tight">
          PDF Data Extractor
        </h1>
        <p className="text-center text-muted-foreground mt-2 text-lg">
          Upload a PDF, extract data using AI, edit it, and save to Google Sheets.
        </p>
      </header>

      <div className="mb-8 p-6 bg-card rounded-xl shadow-xl border border-border">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 items-center">
          <div>
            <Label htmlFor="pdf-upload" className="text-lg font-medium mb-2 block text-foreground">
              Upload PDF Document
            </Label>
            <div className="flex items-center space-x-3">
              <Input
                id="pdf-upload"
                type="file"
                accept=".pdf"
                onChange={handleFileChange}
                className="flex-grow file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-primary/10 file:text-primary hover:file:bg-primary/20 cursor-pointer"
                aria-label="Upload PDF Document"
              />
              <Button
                onClick={handleProcessPdf}
                disabled={isLoading || !pdfFile || isSavingToSheet}
                className="bg-accent hover:bg-accent/90 text-accent-foreground min-w-[150px] transition-all duration-150 ease-in-out transform active:scale-95"
                aria-label="Process PDF for data extraction"
              >
                {isLoading ? (
                  <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                ) : (
                  <Cpu className="mr-2 h-5 w-5" />
                )}
                {isLoading ? "Processing..." : "Process PDF"}
              </Button>
            </div>
             {pdfFile && (
              <p className="mt-3 text-sm text-muted-foreground">
                Selected file: <span className="font-medium text-foreground">{pdfFile.name}</span>
              </p>
            )}
          </div>
          {extractedData && (
             <div className="md:text-right flex flex-col md:flex-row gap-2 items-center justify-end">
                <Button
                    onClick={handleDownloadJson}
                    disabled={!extractedData || isLoading || isSavingToSheet}
                    variant="outline"
                    className="border-primary text-primary hover:bg-primary/5 hover:text-primary min-w-[170px] transition-all duration-150 ease-in-out"
                    aria-label="Download extracted data as JSON"
                >
                    <FileJson className="mr-2 h-5 w-5" />
                    Download JSON
                </Button>
                <Button
                    onClick={handleSaveToSheet}
                    disabled={!extractedData || isLoading || isSavingToSheet}
                    variant="outline"
                    className="border-green-500 text-green-600 hover:bg-green-500/10 hover:text-green-700 min-w-[190px] transition-all duration-150 ease-in-out"
                    aria-label="Save extracted data to Google Sheet"
                >
                    {isSavingToSheet ? (
                        <Loader2 className="mr-2 h-5 w-5 animate-spin" />
                    ) : (
                        <SheetIcon className="mr-2 h-5 w-5" />
                    )}
                    {isSavingToSheet ? "Saving..." : "Save to Sheet"}
                </Button>
            </div>
          )}
        </div>
        {error && (
          <Alert variant="destructive" className="mt-6 shadow-md">
            <AlertTriangle className="h-5 w-5" />
            <AlertTitle>Error</AlertTitle>
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}
      </div>

      <main className="flex-grow grid grid-cols-1 lg:grid-cols-2 gap-6 lg:gap-8">
        <section className="h-[calc(100vh-280px)] min-h-[500px] rounded-xl overflow-hidden border border-border">
          <PdfViewer pdfUrl={pdfObjectUrl} />
        </section>
        <section className="h-[calc(100vh-280px)] min-h-[500px] rounded-xl overflow-hidden border border-border">
          <DataEditor data={extractedData} onDataChange={handleDataChange} />
        </section>
      </main>
    </div>
  );
}
